import React from 'react';

type State = {
  pressedKey: string;
};

type Props = {};

export class App extends React.Component<Props, State> {
  state = {
    pressedKey: '',
  };

  componentDidMount() {
    document.addEventListener('keyup', this.handdlerkey);
  }

  componentWillUnmount() {
    document.removeEventListener('keyup', this.handdlerkey);
  }

  handdlerkey = (event: KeyboardEvent) => {
    this.setState({ pressedKey: event.key });
  };

  render() {
    const { pressedKey } = this.state;

    return (
      <div className="App">
        <p className="App__message">
          {
            pressedKey
              ? `The last pressed key is [${pressedKey}]`
              : 'Nothing was pressed yet'
          }
        </p>
      </div>
    );
  }
}
